/*--------------------adjustment----------------------*/
@import "_source/mob_style";
*,
*::before,
*::after{
	box-sizing: border-box;
}

html{
	font-size: math.div($rootFontSize, 16px) * 100%;
}
html, body{
	margin: 0;
	padding: 0;
	min-height: 100%;
}
body{
	font-family: $tildaReg;
	font-weight: normal;
	font-size: rem($baseFontSize);
	min-width: rem($minWidth);
	position: relative;
	// line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	color: $text;

	.lock & {
		overflow: hidden;
		touch-action: none;
		overflow-behavior: none;
	}
}

