a{
	color: inherit;
}
img{
	vertical-align: top;
}
ul, li{
	margin: 0;
	padding: 0;
}
h1,h2,h3,h4,h5,h6{
	margin: 0;
	font-weight: inherit;
	font-size: inherit;
}
p{
	margin: 0;
}
input
input[type="text"],
input[type="email"],
input[type="tel"],
textarea{
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	font-family: $tildaSemi;
}
.input,
input{
	border-radius: 0;
	outline: none;
	display: block;
	width: 100%;
	border: 0 none;
}
textarea{
	resize: none;
	padding: 0 0;
}
//END input
.wrapper{
	margin: 0 auto;
	padding-left: math.div($containerPaddings, 2) + px;
	padding-right: math.div($containerPaddings, 2) + px;
	max-width: $maxContainerWidth + px;
	// @include row-flex();
	// @include wrapper();
}

[class*="__wrapper"]{
	margin: 0 auto;
	padding-left: rem(math.div($containerPaddings, 2));
	padding-right: rem(math.div($containerPaddings, 2));
	max-width: rem($maxContainerWidth);
	// outline: 1px solid;
	@include min-screen($md){
		padding-inline: rem(24);
	}
}
